












import Vue from "vue";
import { bottomItems } from "@/constants/";

export default Vue.extend({
  data: () => ({
    page: 0,
    show: false,
    items: bottomItems,
  }),
  watch: {
    $route(route) {
      this.setShow(route);
    },
  },
  mounted() {
    this.setShow(this.$route);
  },
  methods: {
    setShow(route: any) {
      let meta = route.meta(route);
      if (meta.noBottomNavigation) {
        this.show = false;
      } else this.show = window.screen.width <= 700;
    },
  },
});
